import React, {useState} from 'react'
import "./navbar.css";
import { Link } from 'react-router-dom';
import useDarkTheme from '../../Hooks/useDarkTheme';

const Navbar = () => {

    const [activeLink, setActiveLink] = useState('home');
    const [toggle, setToggle] = useState('');

    const {theme, toggleTheme} = useDarkTheme();

    const getLinkClass = (link) => {
        if (activeLink === link) return "nav_link active-link" 
        else return 'nav_link'
    }

    const hadleLinkClick = (link) => {
        setToggle('');
        setActiveLink(link)
    }

  return ( 
    <header className="header" id="header">
        <nav className="nav container">
            <a href="#" className="nav_logo"> </a>
            <div className={`${toggle} nav_menu`} id="nav-menu">
                <ul className="nav_list grid">
                    <li className="nav_item">
                        <Link to='/' className={getLinkClass('home')} onClick={() => hadleLinkClick('home')}>
                            <i className="uil uil-estate nav_icon"></i>Home
                        </Link>
                    </li>
                    <li className="nav_item">
                        <Link to='/about' className={getLinkClass('about')} onClick={() => hadleLinkClick('about')}>
                            <i className="uil uil-user nav_icon"></i>About
                        </Link>
                    </li>
                    <li className="nav_item">
                        <Link to='/projects' className={getLinkClass('projects')} onClick={() => hadleLinkClick('projects')}>
                            <i className="uil uil-scenery nav_icon"></i> Projects
                        </Link>
                    </li>
                    <li className="nav_item"  onClick={() => hadleLinkClick('contact')}>
                        <Link to='/contact' className={getLinkClass('contact')}>
                            <i className="uil uil-message nav_icon"></i> Contactme
                        </Link>
                    </li>
                    <li className= {activeLink + " animation"}></li>
                </ul>
                <i className="uil uil-times nav_close" id="nav-close" onClick={() => setToggle('')}></i>
            </div>
            <div className="nav_btns">
                {/* <!--  Theme change button  --> */}
                <i className={`uil ${theme === 'dark' ? 'uil-sun' : "uil-moon"} change-theme`} id="theme-button" onClick={() => toggleTheme()}></i>
                <div className="nav_toggle" id="nav-toggle" onClick={(e) => setToggle('show_menu')}>
                    <i className="uil uil-apps"></i>
                </div>
            </div>
        </nav>
    </header>
  )
}

export default Navbar