import React from 'react'

const Contact = () => {
  return (
     <section className="contact" id="contact">
         <div className="section">
                    <h2 className="section_title">Get in touch</h2>
                    <span className="section_subtitle"></span>

                    <div className="contact_container container grid">
                       <div>
                           <a href='tel: +918147510208' className="contact_information">
                               <i className="uil uil-phone contact_icon"></i>
                               <div>
                                   <h3 className="contact_title">Call Me</h3>
                                   <span className="contact_subtitle">+918147510208</span>
                               </div>
                           </a>

                           <a href='mailto: syed97syed97@gmail.com' className="contact_information">
                               <i className="uil uil-envelope contact_icon"></i>
                               <div>
                                   <h3 className="contact_title">Email</h3>
                                   <span className="contact_subtitle">syed97syed97@gmail.com</span>
                               </div>
                           </a>

                           <div className="contact_information">
                               <i className="uil uil-map-marker contact_icon"></i>
                               <div>
                                   <h3 className="contact_title">Location</h3>
                                   <span className="contact_subtitle">Bengaluru</span>
                               </div>
                           </div>
                       </div> 

                       <form action="" className="contact_form grid">
                           <div className="contact_inputs grid">
                               <div className="contact_content">
                                   <label htmlFor="" className="contact_label">Name</label>
                                   <input type="text" className="contact_input" />
                               </div>
                               
                               <div className="contact_content">
                                   <label htmlFor="" className="contact_label">Email</label>
                                   <input type="email" className="contact_input" />
                               </div>
                               </div>
                               
                               <div className="contact_content">
                                   <label htmlFor="" className="contact_label">Project</label>
                                   <input type="text" className="contact_input" />                              
                               </div>

                               <div className="contact_content">
                                   <label htmlFor="" className="contact_label">Message</label>
                                   <textarea name="" id="" cols="7" rows="7" className="contact_input"></textarea>
                               </div>

                               <div>
                                   <a href="#" className="button button--flex">Send Message
                                       <i className="uil uil-message button_icon"></i>
                                       {/* <!-- <i className="uil uil-message contact_icon"></i> --> */}
                                   </a>
                               </div>
                       </form>
                    </div>
                    </div>
                </section>
  )
}

export default Contact